@import url("https://fonts.googleapis.com/css2?family=Exo+2:wght@100;200;300;400;500;800&display=swap");

body {
  margin: 0px;
  padding: 0px;
  background-color: rgb(24, 24, 24);
  color: rgb(180, 215, 255);
  overflow-x: hidden;
  font-family: "Exo 2", sans-serif;
}

h1,
h2,
h3,
h4,
p {
  margin: 0%;
  margin-top: 0em;
  margin-bottom: 0em;
  padding: 0%;
}

a {
  text-decoration: none;
  color: inherit;
}

input {
  background-color: rgb(45, 55, 66);
  color: azure;
  border: none;
  border-radius: 5px;
  padding: 0.8em;
  font-size: medium;
  margin-top: 0.2em;
  margin-bottom: 0.4em;
  font-family: "Exo 2";
  /* height: auto; */
}

input:focus {
  outline: none;
}

.container {
  display: grid;
  place-items: center;
}

.main-head {
  margin-bottom: 1%;
  font-size: 2rem;
  background-color: rgb(21, 23, 26);
  /* border-radius: 10px; */
  width: 100%;
  text-align: center;
  padding: 10px 0px;
}

button {
  /* background-color: rgba(180, 215, 255, 0.925); */
  margin-top: 10px;
  background-color: transparent;
  /* text-align: center; */
  /* align-content: center; */
  border: 0;
  /* height: 50px; */
  /* width: fit-content; */
  color: rgb(180, 215, 255);
  /* padding: 15px 15px; */
  /* border-radius: 10px; */
  text-decoration: none;
  cursor: pointer;
  margin-left: 8px;
}

.posts {
  /* place-self: center; */
  margin-left: 0.5em;
  margin-right: 0.5em;
  margin-bottom: 1em;
  background-color: rgb(36, 36, 36);
  padding: 0.8em;
  border-radius: 10px;
}

h4.title {
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
  font-size: large;
  font-weight: 500;
  margin-bottom: 0.2em;
}

p.author {
  font-weight: 400;
  margin-bottom: 0.2em;
}

p.points {
  font-size: 12pt;
  margin-bottom: 0.2em;
  font-weight: 500;
}

.search-btn {
  color: aliceblue;
  transition: ease-in-out 200ms;
}

.search-btn:hover {
  color: rgb(180, 215, 255);
}

.lds-dual-ring {
  display: inline-block;
  width: 80px;
  height: 80px;
}
.lds-dual-ring:after {
  content: " ";
  display: block;
  width: 50px;
  height: 50px;
  margin: 8px;
  border-radius: 50%;
  border: 6px solid rgb(180, 215, 255);
  border-color: rgb(180, 215, 255) transparent rgb(180, 215, 255) transparent;
  animation: lds-dual-ring 1.2s linear infinite;
}
@keyframes lds-dual-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.footer {
  margin: 2em;
}

.x-btn {
  color: rgb(180, 215, 255);
  transition: ease-in-out 100ms;
}

.x-btn:hover {
  color: crimson;
}

.filters {
  /* padding: 0.5em 0.8em; */
  /* background-color: rgb(17, 17, 17); */
  margin-bottom: 1em;
  border-radius: 10px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  place-items: center;
  gap: 10px;
}

.sort-btn {
  font-size: 10pt;
  font-family: "Exo 2";
  font-weight: bolder;
  text-transform: uppercase;
}

.sort-btn-active {
  font-size: 12pt;
  color: rgb(24, 24, 24);
  border-radius: 5px;
  background: rgb(180, 215, 255);
  font-family: "Exo 2";
  font-weight: bolder;
  text-transform: uppercase;
  transition: background ease-in-out 500ms;
}

.sort-btn-asc::after {
  content: "   \2191";
  font-family: monospace;
}

.sort-btn-des::after {
  content: "   \2193";
  font-family: monospace;
}

.rm-btn {
  width: 100%;
  text-transform: uppercase;
  font-size: 10pt;
  font-weight: 800;
  letter-spacing: 2px;
  background-color: rgb(36, 36, 36);
  border-radius: 10px;
}

.rs-btn {
  text-transform: capitalize;
  font-size: 10pt;
  font-weight: bold;
  background-color: rgb(45, 55, 66);
  border-radius: 3px;
}
.rs-head {
  font-weight: bolder;
  display: inline-flex;
}

.more-btn {
  text-transform: uppercase;
  font-family: "Exo 2";
  font-size: 14pt;
  font-weight: 600;
  padding: 0.8em 1em;
  background-color: rgb(36, 36, 36);
  border-radius: 5px;
  transition: background-color 0.5s;
}
.more-btn:hover {
  background-color: rgb(61, 61, 61);
}

/* Shimmer styles */
.shimmer {
  background: rgb(36, 36, 36) -webkit-gradient(linear, 100% 0, 0 0, from(#333333), color-stop(0.5, #4b4b4b), to(#333333));

  background-position: -4rem top; /*50px*/
  background-repeat: no-repeat;
  background-clip: width;
  -webkit-text-fill-color: transparent;
  animation-name: shimmer;
  animation-duration: 1.8s;
  animation-iteration-count: infinite;
  background-size: 4rem 100%; /*50px*/
}

@keyframes shimmer {
  0% {
    background-position: -2rem top; /*50px*/
  }

  70% {
    background-position: 100% top; /*200px*/
  }

  100% {
    background-position: -2rem top; /*200px*/
  }
}

.shimmer-posts {
  margin-left: 0.5em;
  margin-right: 0.5em;
  margin-bottom: 1em;
  background-color: rgb(36, 36, 36);
  padding: 0.8em;
  border-radius: 10px;
}

.shimmer-title {
  color: rgb(51, 51, 51);
  width: 65%;
  border-radius: 7px;
  background-color: rgb(51, 51, 51);
}

.shimmer-link {
  color: rgb(51, 51, 51);
  width: 65%;
  border-radius: 7px;
  background-color: rgb(51, 51, 51);
}

.shimmer-author {
  color: rgb(51, 51, 51);
  margin-top: 0.5em;
  width: 25%;
  border-radius: 7px;
  background-color: rgb(51, 51, 51);
}

.shimmer-points {
  color: rgb(51, 51, 51);
  margin-top: 0.5em;
  width: 15%;
  border-radius: 7px;
  background-color: rgb(51, 51, 51);
}

.post-container {
  margin-top: 0.5em;
  width: min(50rem, 100%);
  display: flex;
  flex-direction: column;
}

/* Works on Firefox */
* {
  scrollbar-width: thin;
  scrollbar-color: rgba(167, 202, 241, 0.418) rgb(31, 31, 31);
}

/* Works on Chrome, Edge, and Safari */
*::-webkit-scrollbar {
  width: 10px;

}

*::-webkit-scrollbar-track {
  background: rgb(41,41,41);
}

*::-webkit-scrollbar-thumb {
  background-color: rgb(21, 32, 32);
  border-radius: 10px;
  border: 2px solid rgba(167, 202, 241, 0.418);
}